<template>
  <Nav />
  <Banner />
  <Row
    v-for="(value, key) in API_request"
    :key="key"
    :title="correctName(key)"
    :FetchUrl="getName(key)"
    :isLargeRow="key.includes('Netflix') ? true : false"
  />
</template>

<script>
import Nav from "./Nav";
// import Banner from "./Banner";
import { useStore } from "vuex";
import { defineAsyncComponent } from "vue";
import { API_request } from "../services/request";

export default {
  name: "Homescreen",
  components: {
    Nav,
    Banner: defineAsyncComponent(() =>
      import(
        /* webpackChunkName: "Banner" */ /* webpackPrefetch: true */ "./Banner"
      )
    ),
    Row: defineAsyncComponent(() =>
      import(/* webpackChunkName: "Row" */ /* webpackPrefetch: true */ "./Row")
    ),
  },
  props: {
    msg: String,
  },
  setup() {
    const store = useStore();
    store.dispatch("fetchData");
    Object.entries(API_request).forEach((element) => {
      store.dispatch("fetchRows", element);
    });
    const correctName = (key) => {
      return key
        .replace("fetch", "")
        .split(/(?=[A-Z])/)
        .join(" ");
    };
    const getName = (key) => {
      return key.replace("fetch", "get");
    };
    return {
      API_request,
      correctName,
      getName,
    };
  },
};
</script>

